import { request,getToken,Token } from '../../request.js'
export default {
	data() {
		return {
			uid:0,
			info:[],
			saveinfo:[],
		};
	},
	onLoad(options){
		this.uid = options.id;
		this.getInfo(options.id);
	},
	methods: {
		getInfo(id){
			let that = this;
			request({
				url:"index.php?s=/wap/member/apiGetStaffInfo",
				method:"POST",
				data:{
					uid:id
				}
			}).then(function(res){
				if(res.data.code==1){
					let setInfo = res.data.staff;
					that.info = [setInfo.staff_rate1,setInfo.staff_rate2,setInfo.staff_rate3,setInfo.staff_rate4,setInfo.staff_rate5,setInfo.staff_rate6];
				}
			});
		},
		changeInfo(obj){
			this.info[obj.target.dataset.key] = obj.detail.value;
		},
		saveSet(){
			let saveInfo = this.info;
			request({
				url:"index.php?s=/wap/member/apiEditStaff",
				method:"POST",
				data:{
					uid:this.uid,
					rate1:saveInfo[0],
					rate2:saveInfo[1],
					rate3:saveInfo[2],
					rate4:saveInfo[3],
					rate5:saveInfo[4],
					rate6:saveInfo[5],
				}
			}).then(function(res){
				uni.showToast({
					title:res.data.msg,
					icon: 'none',
				})
			});
		}
	},
}
